<template>
  <h1>Calendario Dailys</h1>
  <div style="justify-content: center; display: flex">
    <table class="tg">
      <tbody>
        <tr>
          <td class="tg-baqh" colspan="4">
            <DatePicker v-model="date" mode="date" :attributes="attrs" />
          </td>
        </tr>
        <tr>
          <td class="tg-yz93 title" colspan="2">Semana del año:</td>
          <td class="tg-lboi" colspan="2">{{ this.moment(date).isoWeek() }}</td>
        </tr>
        <tr>
          <td class="tg-yz93 title" colspan="2">Día del año:</td>
          <td class="tg-lboi" colspan="2">
            {{ this.moment(date).dayOfYear() }}
          </td>
        </tr>
        <tr v-if="isDayOfWeek()">
          <td class="tg-yz93 title" colspan="2">Responsable daily:</td>
          <td class="tg-lboi" style="width: 70px">
            <img
              :src="`${teamActive[turn()].image}`"
              :alt="`${teamActive[turn()].nombre}`"
              style="width: 60px"
            />
          </td>
          <td class="tg-lboi" style="width: 150px">
            {{ teamActive[turn()].nombre }}
          </td>
        </tr>
        <tr v-else>
          <td class="tg-yz93 title" colspan="2">Responsable daily:</td>
          <td class="tg-lboi" colspan="2" style="width: 220px">--</td>
        </tr>
        <tr v-if="isDayOfWeek()">
          <td class="tg-yz93 title" colspan="2">Backup responsable daily:</td>
          <td class="tg-lboi" style="width: 70px">
            <img
              :src="`${teamActive[turnBackUp()].image}`"
              :alt="`${teamActive[turnBackUp()].nombre}`"
              style="width: 60px"
            />
          </td>
          <td class="tg-lboi" style="width: 150px">
            {{ teamActive[turnBackUp()].nombre }}
          </td>
        </tr>
        <tr v-else>
          <td class="tg-yz93 title" colspan="2">Backup responsable daily:</td>
          <td class="tg-lboi" colspan="2" style="width: 220px">--</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import _ from "lodash";
import { DatePicker } from "v-calendar";
import { onBeforeMount, reactive, ref } from "vue";
import momentjs from "moment";
// import FeriadosAPI from "@/api/feriados";
import team from "@/data/team";
import holidays from "@/data/holidays";

export default {
  components: {
    DatePicker,
  },

  setup() {
    const moment = momentjs;

    let attrs = reactive([]);

    let teamActive = _.filter(team, function (o) {
      return o.enabled == null || o.enabled;
    });

    let teamLength = teamActive.length;

    onBeforeMount(() => {
      feriados();
      cumpleanos();
    });

    let date = ref(new Date());
    let isDayOfWeek = () =>
      date.value != null &&
      moment(date.value).weekday() >= 1 &&
      moment(date.value).weekday() <= 5;
    let turn = () =>
      (moment(date.value).weekday() + (moment(date.value).isoWeek() - 1) * 5) %
      teamLength;
    let turnBackUp = () =>
      turn() + 2 > teamLength - 1 ? (turn() + 2) % teamLength : turn() + 2;
    /* let feriados = () => {
      FeriadosAPI.getFeriadosDelAnio(moment(date.value).year()).then((x) => {
        x.forEach((item) => {
          attrs.push({
            key: "feriados",
            highlight: "red",
            popover: {
              label: `Feriado - ${item.nombre}`,
            },
            dates: moment(item.fecha, "YYYY-MM-DD").toDate(),
          });
        });
        date.value = new Date();
      });
    }; */
    let feriados = () => {
      holidays.forEach((item) => {
        attrs.push({
          key: "feriados",
          highlight: "red",
          popover: {
            label: `Feriado - ${item.nombre}`,
          },
          dates: moment(item.fecha, "YYYY-MM-DD").toDate(),
        });
      });
      date.value = new Date();
    };
    let cumpleanos = () => {
      team.forEach((item) => {
        if (!item.birthday) {
          return;
        }

        attrs.push({
          key: "cumpleanos",
          bar: "green",
          popover: {
            label: `Cumpleaños - ${item.nombre}`,
          },
          dates: moment(
            `${item.birthday}-${moment(date.value).year()}`,
            "DD-MM-YYYY"
          ).toDate(),
        });
      });
    };

    return {
      date,
      team,
      teamActive,
      turn,
      turnBackUp,
      isDayOfWeek,
      moment,
      feriados,
      attrs,
      cumpleanos,
    };
  },
};
</script>

<style>
.tg {
  border-collapse: collapse;
  border-spacing: 0;
}
/* .tg td{border-color:black;border-style:solid;border-width:1px;font-family:Arial, sans-serif;font-size:14px;
  overflow:hidden;padding:10px 5px;word-break:normal;} */
.tg td {
  border-color: black;
  border-style: solid;
  border-width: 0px;
  font-family: Arial, sans-serif;
  font-size: 14px;
  overflow: hidden;
  padding: 10px 5px;
  word-break: normal;
}
/* .tg th{border-color:black;border-style:solid;border-width:1px;font-family:Arial, sans-serif;font-size:14px;
  font-weight:normal;overflow:hidden;padding:10px 5px;word-break:normal;} */
.tg th {
  border-color: black;
  border-style: solid;
  border-width: 0px;
  font-family: Arial, sans-serif;
  font-size: 14px;
  font-weight: normal;
  overflow: hidden;
  padding: 10px 5px;
  word-break: normal;
}
.tg .tg-lboi {
  border-color: inherit;
  text-align: left;
  vertical-align: middle;
}
.tg .tg-lqy6 {
  text-align: right;
  vertical-align: top;
}
.tg .tg-yz93 {
  border-color: inherit;
  text-align: right;
  vertical-align: middle;
}
.title {
  font-weight: bold;
  text-align: right;
}
</style>
